:root{
    --primary-color: #F1F1F1;
    --secondary-color: #423f61;
    --tertiary-color: #55725d;
    --fourth-color: #8cb27f;
    --fifth-color: #141c17;

    --text-color1: black;
    --text-hover1: gray;

    --main-font: DMSans, "Helvetica Neue", acumin, "Times New Roman", sans-serif;;

    --navbar-height:4rem;

    overflow-x:hidden;
}

@font-face {
    font-family: acumin;
    src: url("./fonts/Acumin-RPro.woff");
}

@font-face{
    font-family:DMSans;
    src: url("./fonts/DMSans-VariableFont_opsz,wght.ttf");
}

html{
    background-color: var(--secondary-color);
}
body{
    background: linear-gradient(var(--primary-color),#e9e9e9);
    margin:0;
    width:100%;
    height:100%;

}



.container{
    width:100%;
    height:400px;
    position: relative;
}

.box{
    width:50px;
    height:50px;
    background-color: black;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}

.leaflet-container {
    width: 100%;
    height: 60vh;
    }

.testSpace{
    min-height:9rem;
    background-color: var(--secondary-color);
}


.bannerDark{
    width:100%;
    background-color: var(--secondary-color);
    min-height:100rem;

}

.separateBar{
    width:30px;
    height:4px;
    background-color: black;
    margin-left: auto;
    margin-right: auto;
    margin-top:2vw;
}

.separateBarWhite{
    width:30px;
    height:4px;
    background-color: white;
    margin-left: auto;
    margin-right: auto;
    margin-top:2vw;
}

.encompass{
    opacity:1;
    transition: 0.2s opacity;
}

.fade{
    opacity:0;
}

.contentBanner{
    background: white;
    width:80%;
    margin-inline:10%;
    min-height:20rem;
    text-align: center;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);

}

.flexBanner{
    background: white;
    display: flex;
    width:80%;
    padding-inline:10%;
    margin-block:4rem;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);

}

.clearBanner{
    display: flex;
    width:80%;
    padding-inline:10%;
    margin-block:4rem;
}

.seventy{
    flex-basis: 70%;
}

.thirty{
    flex-basis:30%;
}

.sixty{
    flex-basis:60%;
}

.forty{
    flex-basis: 40%;
    display: flex;
    justify-content: center;
    justify-items:center;
}


.centerHeader{
    text-align: center;
    letter-spacing: 0.1rem;
    padding-top:5%;
    font-family: var(--main-font);
    font-size:2rem;
}

.centerSubheader{
    text-align: center;
    letter-spacing: 0.1rem;
    padding-top:5%;
    font-family: var(--main-font);
    font-size:1rem;
}

.centerText{
    text-align: center;
    padding-inline:20%;
    padding-top:4%;
    font-family: var(--main-font);
    font-size:1.5rem;
    line-height:1.5;
}

.leftText{
    text-align: left;
    padding-inline:15%;
    padding-top:4%;
    font-family: var(--main-font);
    font-size:1.3rem;
    line-height:1.5;
}



.fifty{
    flex-basis:50%;
    
}

@media(max-width:1024px){

    
    .fifty{
        flex-basis:100%;
    }

    .sixty{
        flex-basis:100%;
    }

    .forty{
        flex-basis:100%;
    }

    .seventy{
        flex-basis:100%;
    }

    .thirty{
        flex-basis:100%;
    }

    .flexBanner{
        width:100%;
        margin-inline:0;
        padding-inline:0;
        flex-wrap:wrap;
    }

    .clearBanner{
        flex-wrap:wrap;
    }

    .clearBanner.reverse{
        flex-wrap:wrap-reverse;
    }

    .flexBanner.reverse{
        flex-wrap:wrap-reverse
    }

    
    .leftText{
        padding-inline:10%;
    }

    

    .centerText{
        padding-inline:10%;
    }
}

@media(max-width:700px){
    
    .contentBanner{
        width:100%;
        margin-inline:0;
    }

    .clearBanner{
        width:100%;
        padding-inline:0;
    }


}